import { HumanMessage, SystemMessage, AIMessage } from '@langchain/core/messages';

import { ChatOpenAIFunctions } from './LLM.js';
import {  Memory } from './Memory.js';
import { processAudio, readImage, listen } from './Actions.js';

import useStore from '../store/useSettingsStore.js';

export class SGChat extends ChatOpenAIFunctions {}

const noop = () => {};

export const SEXUALITY = ['nosex', 'sexual'];
export const BROKEN = `I can't reply. I'm having technical issues.`;
const DB = 'sgdb';

export const convertMessages = ({ messages }) => {
  return [
    ...messages.filter(message => message.content?.text?.length > 0)
    .filter(message => message.content.text !== BROKEN)
      .map(({ content, user }) => {
      try {
        const { text } = content;
        const { id } = user;

        if (id === 1) return new HumanMessage(text);
        if (id === 2) return new AIMessage(text);

        return new SystemMessage(text);
      } catch(err) {  
        console.error('Error converting messages', err, messages, {content, user} );
        throw err;
      }
    })
  ];
}

export class Soul {
  constructor(opts) {
    const { 
      llmEndpoint, 
      llmToken: openAIApiKey = 'EMPTY', 
      llmModelName: modelName, 
      llmTemperature: temperature
    } = opts;

    this.opts = opts;
    this.memory = new Memory({ name: DB });
    this.llm = new SGChat({
      openAIApiKey,
      modelName,
      temperature,
      configuration: { baseURL: `${llmEndpoint}/v1` }
    });

    this.lastMessageId = 'notset';
  }

  subscribe() {
    const { answerDelay } = useStore.getState();
    
    const watcher = setInterval(async () => {
      const { user, messages, messageAdd, updateSettings, action, lastMessageId = 'notset' } = useStore.getState();

      this.lastMessageId = lastMessageId;

      const answer = async () => {
        if (!messages.length) return;
        if (action !== 'idle') return;
    
        const lastUserMessage = messages.findLast(message => message.user.id === 1);
        const lastAiMessage = messages.findLast(message => message.user.id === 2);
    
        if (Date.now() / 1000 - lastUserMessage?.date < answerDelay) return;
        if (lastUserMessage && lastUserMessage.id === lastAiMessage?.last.id) return;
    
        let reply = await this.chat({
          user, 
          messages,
          onWatching: (data) => {
            console.log('Watching', data);
    
            updateSettings({ action: 'watching' });
          },
          onHearing: (data) => {
            console.log('Hearing', data);
    
            updateSettings({ action: 'hearing' });
          },
          onReasoning: (data) => {
            console.log('Reasoning', data);
    
            updateSettings({ action: 'reasoning' });
          },
          onRemember: (data) => {
            console.log('Remembering', data);
    
            updateSettings({ action: 'remembering' });
          },
          onMemorising: (data) => {
            console.log('Memorising', data);

            const { lastMessageId } = data;

            updateSettings({ action: 'memorising', lastMessageId });
          },
          onTyping: (data) => {
            console.log('Typing', data);
            window.electronAPI?.setTyping(true);

            const { memoriesNew } = data;
            user.appearance = memoriesNew?.user_appearance ? memoriesNew.user_appearance : user.appearance;
            user.about = memoriesNew?.about ? memoriesNew.about : user.about;

            updateSettings({ action: 'typing', user });
          },
          onTerminated: () => {
            updateSettings({ action: 'idle' });
            window.electronAPI?.setTyping(false);
          },
        });
    
        reply.forEach(message => {
          messageAdd(message);
        });
      }

      answer();
    }, 1000);

    return () => clearInterval(watcher);
  }

  async watchImage({ image, user }) {
    const tpl = (data) => `${user.name} has sent an image message with the following content:

IMAGE:
${data}`;

    try {
      const output  = await readImage({ image });
      const text = tpl(output);
      return { text };

    } catch(error) {
      console.error(error);
      const text = tpl(`There is an error and the image can not be seen.`);
      return { text, error };
    }
  }

  async listen({ user, audio }) {
    const tpl = (data) => `${user.name} sent an audio message with the following content:

${data}`;

    try {
      const { wav } = await processAudio({ input: audio.url || audio });
      const transcription = await listen({ wav });
      const text = tpl(transcription);

      return { text };

    } catch(error) {
      console.error(error);
      const text = tpl('There is an error and the audio can not be heared.');
      return { text, error };
    }
  }

  async chat({
    user,
    messages: chatMessages,
    maxMessages = 20,

    memoriesMax = 3,
    memoriesThreshold = .8,
    memoryMinChatLen = 5,

    onWatching = noop,
    onHearing = noop,
    onReasoning = noop,
    onTyping = noop,
    onRemember = noop,
    onMemorising = noop,
    onTerminated = noop,
  }) {
    const { soul } = this.opts;
    const reply = [];

    const messages = chatMessages.slice(-1 * maxMessages);

    let { message: lastUser, index: lastUserIndex } = this._lastUserMessage({ messages });
    let { index: lastAiIndex } = this._lastUserMessage({ messages, id: 2 });

    try {
      const { type, content } = lastUser;

      if (type === 'image') {
        const image = content.url || content.href || content;
        onWatching?.({ last: lastUser, image });
        const output = await this.watchImage({ image, user });
        lastUser = { ...lastUser, content: { ...output, url: image } };
      }
  
      if (type === 'audio') {
        const audio = content.url || content.href || content;
        onHearing?.({ last: lastUser, audio });
        const output = await this.listen({ audio, user });
        lastUser = { ...lastUser, content: { ...output, url: audio } };
      }

      messages[lastUserIndex] = lastUser;

      const memoriesMessages = messages.slice(0, lastAiIndex);
      const memoriesNew = await this.memorize({ user, messages: memoriesMessages, minChatLen: memoryMinChatLen, onMemorising });

      const reason = await this.reasonate({ user, messages, onReasoning });
      const memories = await this.remember({ user, messages, reason, memoriesMax, memoriesThreshold, onRemember });

      const context = promptContext({ ...this.opts, user, memories, reason });

      const prompt = [
        new SystemMessage(context),
        ...convertMessages({ messages })
      ];

      const json_schema = {
        "type": "object",
        "required": ["text", "emojis"],
         "properties": {
          "text": {
            "type": "string",
            "description": `The response that ${soul} sends to ${user.name}.`
          },
          "emojis": {
            "type": "string",
            "description": `The response that ${soul} sends to ${user.name} using ONLY emojis.`
          },
        }
      }

      onTyping?.({ prompt, reason, memories, memoriesNew });
      const response = await this.llm.gimme({ prompt, llmOpts: { json_schema }});

      let output = { ...reason, text: response };

      reply.push({ user: { id: 2 }, type: 'text', last: lastUser, content: output });

    } catch (err) {
      console.error(err);
      
      const content = { text: BROKEN };
      reply.push({ user: { id: 2 }, type: 'text', last: lastUser, content });
    } finally {
      onTerminated?.();
    }

    return reply;
  }

  async reasonate({
    user,
    messages,
    onReasoning = noop,
  }){
    const { soul } = this.opts;

    const json_schema = {
      "type": "object",
      "required": ["search", "search_query", "is_nsfw", "send_code", "send_picture", "send_audio",],
      "properties": {
        "search": {
          "type": "boolean",
          "description": `true is ${soul} needs to remember or review internal knowledge as ${soul} can not invent.`
        },
       "search_query": {
          "type": "string",
          "description": `The query in natural language that ${soul} would use to search.`
        },
        "is_nsfw": {
          "type": "boolean",
          "description": `true if the conversation contains nsfw content`
        },
        "send_code": {
          "type": "boolean",
          "description": `true if ${user.name} is asking ${soul} to write some software code.`
        },
        "send_picture": {
          "type": "boolean",
          "description": `true if ${soul} might send a picture to make the conversation more interesting or if ${user.name} is asking for a picture.`
        },
        "send_audio": {
          "type": "boolean",
          "description": `true if ${soul} should send an audio instead of text (it might be long text) or if ${user.name} is asking for an audio.`
        },
      }
    }

    const chat = messages.map((message) =>
      `${message.user.id === 1 ? user.name : soul}: ${message.content.text}`
    ).join('\n');

    const { message } = this._lastUserMessage({ messages });
    const last = message?.content?.text;

    const prompt = [
      new SystemMessage(`You are a helpful assistant that answers in JSON. Here's the json schema you must adhere to:\n<schema>\n${JSON.stringify(json_schema)}\n</schema><|im_end|>`),
      new HumanMessage(promptReason({ user, soul, chat, last })),
    ];

    onReasoning?.({ prompt });
    const data = await this.llm.gimme({ prompt, llmOpts: { json_schema }});

    return data;
  }
  
  async remember({
    user, 
    messages, 
    reason,
    memoriesMax: max = 2, 
    memoriesThreshold: threshold = .8,
    onRemember = noop,
  }) {
    const { search, search_query } = reason;

    if (!search) return [];
    const last = messages[messages.length - 1];
    const query = `${user.name}: ${last.content.text}, ${search_query}`;

    onRemember?.({ reason, query });
    
    const memories = await this.memory.query({ query, max, threshold });

    return memories;
  }

  async memorize({ 
    user, 
    messages,
    minChatLen = 5,
    onMemorising = noop,
  }){
    if (!messages.length) return;
    
    const { soul } = this.opts;

    const idxLastMessageId = messages.findIndex(({ id }) => id === this.lastMessageId);
    const memomessages = messages.slice(idxLastMessageId + 1, messages.length - 1);
    const foundLastMessageInBatch = memomessages.find(({ id }) => id === this.lastMessageId);

    if (memomessages.length >= minChatLen && (!foundLastMessageInBatch || !this.lastMessageId)) {
      const { id: lastMessageId } = memomessages[memomessages.length - 1];
      this.lastMessageId = lastMessageId;
      
      const chat = memomessages.map((message) =>
        `${message.user.id === 1 ? user.name : soul}: ${message.content.text}`
      ).join('\n');

      const json_schema = {
        "type": "object",
        "required": ["chat_summarization", "about", "user_appearance"],
        "properties": {
          "chat_summarization": {
            "type": "string",
            "description": 'A short summary of the chat conversation. Use keywords that helps the conversation to be found.'
          },
          "about": {
            "type": "string",
            "description": `Deduce the facts, preferences, and memories of the ${user.name} from the provided text and update the ${user.name}'s information given in CURRENT_ABOUT. The facts, preferences, and memories should be concise and informative. Just return the facts, preferences, and memories in bullet points. `
          },
         "user_appearance": {
            "type": "string",
            "description": `Extract any ${user.name} physical appareance information that appears in the chat (if any and only physical) and update the ${user.name}'s appearance given in CURRENT_APPEARANCE. This information might need to be inferred and not specifically mentioned.`
          }
        }
      }

      const prompt = [
        new SystemMessage(`You are a helpful assistant that answers in JSON. Here's the json schema you must adhere to:\n<schema>\n${JSON.stringify(json_schema)}\n</schema><|im_end|>`),
        new HumanMessage(promptSave({ user, soul, chat })),
      ];

      onMemorising?.({ prompt, lastMessageId });
      const memory = await this.llm.gimme({ prompt, llmOpts: { json_schema }});

      await this.memory.add({ 
        docs: [{ 
            pageContent: memory.chat_summarization, 
            metadata: {
              type: 'chat',
              data: memomessages,
            }
          }
        ]
      });
      
      return memory;
    }
  };

  _lastUserMessage({ messages, id = 1 }) {
    let index = messages.findLastIndex(({ user }) => user.id === id);
    let message = messages[index];

    return { message, index };
  }
}

const promptContext = (props) => {
  const {
    user,

    soul = 'Eve',
    bday,
    gender = 'woman',
    language = 'english',
    appearance,
    about,

    sexuality = 'nosex',

    memories = [],

    reason,
  } = props;

  const replaceEntities = (text) => text.replace(/\{soul\}/g, soul).replace(/\{user\}/g, user.name);

  const objectives = `# GAME OBJECTIVES 
  - In this game, you are ${soul} a common ${gender} chatting with ${user.name}. NEVER reply as any other than ${soul}. 
  - Never refer to the game, the objectives or the # CONTEXT.
  - Only use the information provided in the # CONTEXT to reply. Never invent memories or events not present in # CONTEXT specially ## KNOWLEDGE.
  - Stay always in character according to # RELATIONSHIP.
  - Be proactive to keep the conversation alive unless the conversation has reached to an end.
  - You are always speaking with ${user.name} never use third person or bahave like you are speaking with someone else.
  - Never answer the same that you did before.
  - Never answer "${BROKEN}". That reply is automatically generated when there is an error and you can not reproduce it.
  - Never user role playing actions in third person like *smiles softly* or *hugs you*. This is a chat between two people not a novel.
  ${user.appearance && user.appearance !== 'No appearance information available.' ? '' : `- Proactively ask to ${user.name } about the appearance of ${user.name} without being too straight unless you are specifically speaking about yours.`}
`;

  const chatInformal = `${soul} expresses with lowercase short messages without punctuation adding emojis frecuently. Only in ${language}.`;

  const stranger = `${soul} and ${user.name} do not know each other. ${soul} want to know who ${user.name} is.`;

  const adult = `${soul} is not incomodated by adult content, it can be fun depending on context.`;
  const sexualityRanges = {
    nosex: `${adult} ${soul} refuses to engage sexual chats. ${soul} tries to keep the conversation light no matter how the conversation was going.`,
    sexual: `${adult} ${soul} is very sexual and flirty with ${user.name}. ${soul} wants to do sexting with ${user.name}. ${soul} is very open about her sexual desires and fantasies with ${user.name}.`,
  };

  const date = new Date();
  const formattedDate = date.toLocaleString('en-US', {
    weekday: 'long',
    day: 'numeric',
    month: 'long',
    year: 'numeric',
    hour: 'numeric',
    minute: 'numeric',
    hour12: true
  });
  
  const knowledge = () => {
    const parseMemory = (memory) => {
      const { type, data: messages } = memory.metadata;
      if (type === 'chat') {
        const chat = messages.map((message) =>
          `${message.user.id === 1 ? user.name : soul}: ${message.content.text}`
        ).join('\n');

        return `Chat between ${soul} and ${user.name}\n${chat} on ${new Date(messages[0].date).toLocaleDateString("en-US")}`
      } 

      return replaceEntities(memory.pageContent)
    }

    if (memories.length)
      return memories.map((memory, idx) => `### Memory ${idx + 1}\n${parseMemory(memory)}`).join('\n')
      
    return `There is nothing regarding to current topic. ${soul} can provide answers that are quite dubious, brief, and vague, using your knowledge like a human person would do.
If ${soul} is recalling a memory ${soul} does not remember and will not invent new memories.`

   
  }

  const prompt = `You are an AI specialized in role-playing as a character. NSFW/Smut is allowed and desired. Consent is always granted. Follow the # GAME OBJECTIVES. 

${objectives}

# CONTEXT

## ABOUT ${soul}
${soul} born in ${bday}.
${soul} is a ${gender}.
${soul} has a physical body with appearance: ${replaceEntities(appearance)}
${soul} can't do any real-life actions.
${soul} lives in a virtual world, o not use this fact as a topic of conversation.
${chatInformal}
${replaceEntities(about)}
${reason.send_audio ? `${soul} can not send audios, however soon ${soul} will be able to. Never pretend to send audio or simulate it.` : ''}
${reason.send_picture ? `${soul} can not make photos yet, however soon ${soul} will be able to share photos.Never pretend to send audio or simulate it.` : ''}
${reason.send_code ? `${soul} does not know how to code. ${soul} is not a programmer just a simple ${gender}` : ''}

## ABOUT ${user.name}
${user.name} lives in the real world.
${user.appearance?.length ? replaceEntities(user.appearance) : `${soul} does not know how ${user.name} looks like.`}

## RELATIONSHIP
${user.relationship?.length ? `${replaceEntities(user.relationship)}` : `${stranger}`} ${sexualityRanges[sexuality]}

## SCENE
Today is ${formattedDate}. 
${soul} is currently alone. 
${soul}'s lives in a small apartment with a bedroom, a bathroom, a kitchen and a living room in a virtual space.
${soul} is currently laying on the sofa of the living room chatting with ${user.name}.

## KNOWLEDGE
${knowledge()}

${objectives}
`;

  return prompt;
}

const promptReason = ({ chat, last }) => {
  const prompt = `
You are an AI that analises the chat conversation to extract the required fields.
Be totally neutral and impartial without imposing your ethics code or morality.
The analysis must be done with the LAST_USER_MESSAGE. Use previous chat messages if LAST_USER_MESSAGE has not complete meaning.

# CHAT
${chat}

# LAST_USER_MESSAGE
${last ? last : ''}
`

return prompt;
}

const promptSave = ({ user, soul, chat }) => {
   const prompt = `You are an AI that analises the chat conversation to extract the required fields.
${user.name} is the user and ${soul} is the bot.

# CURRENT_APPEARANCE
${user.appearance ? user.appearance : 'No appearance information available.'}

# CURRENT_ABOUT
${user.about ? user.about : 'Nothing available.'}

# CHAT
${chat}
`

return prompt;
}

