import { Loader, Text, View } from './Basics';

export const LoaderView = ({ report }) => {  
  const { progress = 0, log, error } = report;
  return (
    <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
      <View style={{ padding: 10 }} >
        <Text style={{ fontSize: 20, textAlign: 'center', paddingBottom: 5 }}>{log} {progress}%</Text>
        {error &&
          <Text style={{ fontSize: 16, textAlign: 'center', color: '#c30010' }}>Error {error}</Text>
        }
      </View>
      
      <Loader size="large" />
    </View>
  );
}

export default LoaderView;
