import {
  ActivityIndicator,
  Image,
  Pressable,
  Text as RNText,
  TextInput as RNTextInput,
  View as RNView,
  Dimensions
} from 'react-native';

import RNCheckBox from 'expo-checkbox';

import { Icon as NIcon } from './Icons';

const fontFamily = 'Arial';
export const SIZES = { 
  small: 12,
  medium: 14,
  large: 16,
  title: 18,
};

export const COLORS = {
  primary: '#2D69AF',
  warning: '#fec514',
  danger: '#C41818',
  success: '#4DC418',
  secondary: '#4DC418',

  borderColor: '#E2E6F0',
  backColor: '#F7F7F7',

  app: '#212121',

  textPrimary: '#fff',
  textSecondary: '#6C6C6C'
}

export const HRule = ({ color = COLORS.borderColor, width = 1, style }) => {
  const defStyle = { borderBottomWidth: width, borderBottomColor: color };

  return <View style={[style]} ><View style={[defStyle]} /></View>
}

export const Title = ({ style, children }) => {
  const defStyle = { fontSize: SIZES.title, paddingVertical: 5 };

  return <Text style={[defStyle, style]}>{children}</Text>
}

export const Text = ({ children, style, ...rest }) => {
  const defStyle = { fontFamily, color: COLORS.textPrimary, fontSize: SIZES.medium };

  return <RNText style={[defStyle, style]} {...rest} >{children}</RNText>
}

export const TextInput = ({ style, label, labelStyle, ...rest }) => {
  const defStyle = { padding: 10, color: COLORS.textPrimary, borderColor: COLORS.borderColor, borderWidth: 1, borderRadius: 10, outlineStyle: 'none' };

  return (
    <View style={{ paddingVertical: 10 }}>
      {label &&
        <Text style={[ { fontSize: SIZES.small }, labelStyle ]}>{label}</Text>
      }
      <RNTextInput style={[defStyle, style]} placeholderTextColor={COLORS.textSecondary} {...rest} />
    </View>
  )
}

export const ImageButton = ({ size = 50, source, onPress, style }) => (
  <Pressable style={[{ width: size, height: size, borderRadius: 50, alignItems: 'center', justifyContent: 'center' }, style]} onPress={onPress}>
    <Image source={source} style={[{ width: size, height: size, borderRadius: 50 }, style]}  />
  </Pressable>
)

export const CheckBox = (props) => <RNCheckBox {...props} />

export const View = (props) => <RNView {...props} />

export const Loader = (props) => <ActivityIndicator {...props} />

export const Button = ({ title, onPress, color = COLORS.primary, style, styleText, disabled }) => {
  const backgroundColor = COLORS[color] || color;

  const defStyle = { backgroundColor, fontFamily, fontSize: SIZES.large, padding: 10, borderRadius: 5 };
  const textStyle = { color: '#fff', textAlign: 'center', fontSize: SIZES.large }; 
  return <Pressable onPress={onPress}  disabled={disabled}
    style={({ pressed }) => [ defStyle, style, { opacity: pressed ? 0.5 : 1 } ]}>
    <Text style={[textStyle, styleText]}>{title}</Text>
  </Pressable>
}

export const ButtonIcon = ({ children, icon, onClick, style, color = COLORS.textSecondary , size = 18, testID }) => {
  return (
    <Pressable
      testID={testID}
      onPress={onClick}
      style={[
        { flexDirection: 'row', alignItems: 'center', justifyContent: 'center' },
        style
      ]}
    >
      { icon && <Icon {...{icon, size, color }} />}
      {children}
    </Pressable>
  )
};

export const Icon = ({ icon, size, color = COLORS.textPrimary }) => {
  const styling = { icon, size, color: COLORS[color] || color }
  return <NIcon {...styling} />
}

export const Screen = ({
  children,
  style,
  visible,
  onClose,
  closeable,
  color = COLORS.app,
}) => {
  if (!visible) return null;

  const { height, width } = Dimensions.get('window');
  const defStyle = { flex: 1, position: 'absolute', backgroundColor: color, width, height };
  
  return (
    <View style={[defStyle, style]}>

      {closeable && (
        <View style={[{ position: 'absolute', top: 5, right: 5, zIndex: 1000 }]}>
          <ButtonIcon size={20} onClick={() => onClose?.()} icon='x' />
        </View>
      )}

      {children}
    </View>
  );
}
