import { useEffect } from 'react';

import { gsap } from 'gsap';

import useSettingsStore from '../store/useSettingsStore';

import { View, Text, SIZES } from './Basics';
import './Soul.css';

const colors = {
  reasoning: '#B0C4DE',    
  remembering: '#FFDAB9',  
  typing: '#B0E0E6',
  introspecting: '#FFFACD',
  idle: '#B0E0E6'
}

export const Pulse = ({ action = 'idle' }) => {
  const color = colors[action];
  const isIdle = action === 'idle';

  useEffect(() => {
    gsap.to(".ring", {
      scale: 1.75,
      opacity: 0,
      duration: isIdle ? 4 : 2,
      stagger: {
        each: 0.3,
        repeat: -1
      }
    });
  }, [action]);

  if (isIdle) return (
    <div className="pulse" style={{ backgroundColor: color }}>
      <div className="ring"></div>
    </div>
  )

  return (
    <div className="pulse" style={{ backgroundColor: color }}>
      <div className="ring"></div>
      <div className="ring"></div>
      <div className="ring"></div>
      <div className="ring"></div>
    </div>
  );
};

export const Soul = () => {
  const { action } = useSettingsStore();
  const isBusy = false; // action !== 'idle';

  return (
    <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
      <Pulse key={action} action={action} />
      
      {isBusy &&
        <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center', position: 'absolute' }}>
          <Text size='large' style={{ color: '#fff', fontSize: SIZES.large }}>Eve is {action}</Text>
        </View>
      }
    </View>
  )
}

export const Header = () => {
  const { name = 'Eve', action } = useSettingsStore();

  return (
    <View style={{ flexDirection: 'row', alignItems: 'flex-end' }} >
      <View style={{ width: 80, height: 80, justifyContent: 'center', alignItems: 'center' }} >
        <View style={{ width: 50, height: 50 }} >
          <Soul />
        </View>
      </View>

      <View style={{ flex: 1, padding: 10 }} >
        <Text style={{ color: '#fff', fontSize: 18 }} >{name}</Text>
        <Text style={{ color: '#fff', fontSize: 12 }} >{action}</Text>
      </View>
        
    </View>
  )
}

export default Header;
