import React, { useEffect } from 'react';

import { Text } from './Basics';

import useSettingsStore from '../store/useSettingsStore';

export const Telegram = () => {
  const { messages, messageAdd, telegram } = useSettingsStore();

  useEffect(() => {
    if (!window?.electronAPI?.telegramStart) {
      console.warn('No Telegram API available');
      return
    }

    window.electronAPI.telegramStart({ key: telegram });
    window.electronAPI.onMessage(({ type, message: content }) => {
      const { message_id: id, href: url, text } = content;
      const message = {
        id,
        type,
        content: { text, url },
        src: 'telegram'
      };

      messageAdd(message);
    });

    return () => {
      window?.electronAPI?.removeMessageListener();
    };
  }, [telegram]);

  useEffect(() => {
    if (!messages.length) return;
    const [ message ] = messages.slice(-1);

    window?.electronAPI?.sendMessage(message);
  }, [messages]);

  const token = telegram ? `${telegram.slice(0, 4)}...${telegram.slice(-4)}` : '';
  return (
    <Text>Telegram:{token}</Text>
  );
}

export default Telegram;
