export class Memory {
  constructor({ name } = {}) {
    if (!name) 
      throw new Error('Memory requires a name');
    
    this.name = name;
    this.indexname = `${name}.index.data`;
    this.dbname = `${name}.db`;
    this.indexdbname = `${name}.indexdb`;

    this.init();
  }

  async add(docs) {
    await this.init();

    const embeddings = await this.embeddings.embedDocuments(docs.map(({ pageContent }) => pageContent));
    const labels = this.index.addItems(embeddings, true);

    let idx = 0;
    for (const doc of docs) {
      const id = doc.id || uuid();
      await this.db.put(id, doc);
      await this.indexdb.put(labels[idx], id);

      idx++;
    }

    await this.index.writeIndex(this.indexname);
  }

  async query({ query, max = 3, threshold = 0.1 }) {
    await this.init();

    const v = await this.embeddings.embedQuery(query);
    const { distances, neighbors } = this.index.searchKnn(v, max, undefined);

    const results = [];
    for (let i = 0; i < neighbors.length; i++) {
      const score = 1 - distances[i];
      const neighbor = neighbors[i];
      const docid = await this.indexdb.get(neighbor);
      const doc = await this.db.get(docid);
      
      results.push({ ...doc, score });
    }

    return results.filter(({ score }) => score > threshold);
  }

  async init() {
    const size = 100000;
    const metric = 'cosine';
    const modelName = 'Xenova/multilingual-e5-base';

    if (this.db) return;

    this.embeddings = await new HuggingFaceTransformersEmbeddings({ modelName });
    const vlen = (await this.embeddings.embedQuery('hello')).length;

    const hnswlib = await loadHnswlib();
    hnswlib.EmscriptenFileSystemManager.setDebugLogs(true);
    this.index = new hnswlib.HierarchicalNSW(metric, vlen, this.indexname);

    if (!hnswlib.EmscriptenFileSystemManager.checkFileExists(this.indexname)) {
      console.log('creating index');
      this.index.initIndex(size, 48, 128, 100);

      await this.add(memories);

    } else {
      console.log('reading index')
      this.index.readIndex(this.indexname, size);
    }

    this.index.setEfSearch(32);

    this.indexdb = new BrowserLevel(this.indexdbname);
    this.db = new BrowserLevel(this.dbname, { valueEncoding: 'json' });

    console.log(await this.query({ query: 'have you seen the gentleman tv series?' }));
  }
}
