import { create } from 'zustand';
import {
  persist,
  createJSONStorage,
} from 'zustand/middleware';

const STOR_ID = 'sg-settings-v1.7';

export const characters = {
  eve: {
    soul: 'Eve',
    appearance: '{soul} is a white caucasian woman. Beautiful, with dark hair, brown eyes and big lips. Slim and quite tall.',
    about: '{soul} likes to chat, walk and exercising.',
    bday: '01-01-2000',
  },
  adam: {
    soul: 'Adam',
    appearance: '{soul} is a white caucasian man. Handome, with dark hair, brown eyes and trimmed bear. Athletic and tall.',
    about: '{soul} likes to chat, walk and exercising.',
    bday: '02-01-2000',
  }
}

const DEFAULTS = { 
  completedWizard: false,
  acceptedTerms: false,
  
  // telegram: '7114345856:AAEh7ngT3L3G8vdrzlxuqS5GBJUZW044ycQ',
  telegram: undefined,
  
  llmEndpoint: 'http://localhost:9697',
  llmToken: undefined,
  llmModelName: 'soulgarden-1',
  llmTemperature: 0.90,
  
  user: {
    name: '',
    appearance: '',
    about: '',

    relationship: `{soul} is loving, caring and affectionate toward {user} but never cloying. `,
    // relationship: `{soul} is loving, caring and affectionate toward {user}. {soul} is not shy about showing her feelings for {user}.`,
  },
  
  answerDelay: 10,
  action: 'idle',

  ...characters.eve,

  language: 'english',
  sexuality: 'nosex',
  scene: '',
  
  lastMessageId: undefined,

  messages: [],
}

const useSettingsStore = create(persist(
  (set, get) => ({
    ...DEFAULTS,
    updateSettings: (newSettings) => set((state) => ({ ...state, ...newSettings })),

    messageAdd: message => {
      if (message.type === 'typing') return;

      const ts = Date.now() / 1000 | 0
      message.id = message.id || ts;
      message.user = message.user || { id: 1 };
      message.date = ts;
    
      const { messages } = get();
      messages.push(message);
      set(() => ({ messages: messages.slice(-100) }));
    },

    setCharacter: character => {
      set(() => ({ ...characters[character] })); 
    },
  }),
  {
    name: STOR_ID,
    storage: createJSONStorage(() => localStorage),
    partialize: ({
      action,
      ...rest
    }) => ({ ...rest })
  })
);

export default useSettingsStore;
