import React, { useEffect, useRef, useState } from 'react'
import { Dimensions, View } from 'react-native'
import Wizardd from 'react-native-wizard'

import { Button, COLORS, CheckBox, ImageButton, Text, TextInput, Title } from './Basics'
import useSettingsStore from '../store/useSettingsStore'

const NavButtons = ({ isNextDisabled, isFirstStep, isLastStep, onPrev, onNext, onAccept }) => {
  return (
    <View style={{ width: '100%', flexDirection: 'row', justifyContent: 'flex-end', paddingTop: 10 }}>
      {!isFirstStep && <Button title="Prev" onPress={onPrev} />}
      {!isLastStep && <View style={{ paddingLeft: 10 }}><Button title="Next" onPress={onNext} color={isNextDisabled ? COLORS.textSecondary : COLORS.primary} disabled={isNextDisabled} /></View>}
      {isLastStep && <View style={{ paddingLeft: 10 }}><Button title="Finish" onPress={onAccept} color={isNextDisabled ? COLORS.textSecondary : COLORS.secondary}  disabled={isNextDisabled} /></View>}
    </View>
  )
}

const Dot = ({ key, color }) => (
  <View
    key={key}
    style={{
      width: 10,
      marginHorizontal: 6,
      height: 10,
      borderRadius: 5,
      backgroundColor: color,
    }}
  />
)

const StepView = ({ children, title }) => {
  const { width } = Dimensions.get('window');
  return (
    <View style={{ width: width - 10 }}>
      <Title style={{ textAlign: 'center' }}>{title}</Title>
      <View style={{ flex: 1, paddingTop: 10 }}>
        {children}
      </View>
    </View>
  )
}

const Adan = require('../assets/adan.png');
const Eve = require('../assets/eve.png');
const Step0 = () => {
  const { soul, appearance, setCharacter } = useSettingsStore();
  const selectedStyle = { borderWidth: 2, borderColor: COLORS.secondary };
  const character = soul.toLowerCase();

  return (
    <StepView title="Eve or Adam">
      <View style={{ flexDirection: 'row', justifyContent: 'center' }}>
        <ImageButton size={80} style={character === 'eve' ? selectedStyle : undefined } source={Eve} onPress={()=>{ setCharacter('eve') }}/>
        <View style={{ width: 10 }} />
        <ImageButton size={80} style={character === 'adam' ? selectedStyle : undefined }  source={Adan} onPress={()=>{ setCharacter('adam') }}/>
      </View>
    </StepView>
  )
}

const Step1 = () => {
  const { updateSettings, user } = useSettingsStore();
  const handleChange = (name) => updateSettings({ user: { ...user, name } });

  return (
    <StepView title="What's your name?">
      <TextInput onChangeText={handleChange} 
        placeholder="Popeye" 
        value={user.name}
        />
    </StepView>
  )
}

const Step2 = () => {
  const placeholder = `I'm a muscular male sailor with squinty eyes and large forearms, sporting a corncob pipe, dressed in a sailor's shirt and bell-bottom pants.`;

  const { updateSettings, user } = useSettingsStore();
  const handleChange = (description) => updateSettings({ user: { ...user, description } });

  return (
    <StepView title="What do you look like?">
      <TextInput
        onChangeText={handleChange} 
        placeholder={placeholder}
        multiline
        numberOfLines={10} 
        value={user.description}
        />
    </StepView>
  )
}

const Step3 = () => {
  const placeholder = `I love sailing the seas and eating spinach. I'm always ready to fight Bluto.`;

  const { updateSettings, user } = useSettingsStore();
  const handleChange = (about) => updateSettings({ user: { ...user, about } });

  return (
    <StepView title="Tell me something about yourself.">
      <TextInput
        placeholder={placeholder}
        onChangeText={handleChange} 
        multiline
        numberOfLines={10} 
        value={user.about}
        />
    </StepView>
  )
}

const Step4 = () => {
  const { updateSettings } = useSettingsStore();
  const [nsfw, setNsfw] = useState(false);

  const handleChange = (val) => {
    setNsfw(val);
    updateSettings({ sexuality: val? 'sexual' : 'nosex' });
  }

  return (
    <StepView title="I'm 18 years old or older">
      <View style={{ flexDirection: 'row', justifyContent: 'center' }}>
        <Text style={{ paddingRight: 10 }}>Enable NSFW</Text>
        <CheckBox value={nsfw} onValueChange={handleChange} />
      </View>
    </StepView>
  )
}

const Step5 = () => {
  const { updateSettings } = useSettingsStore();
  const handleChange = (telegram) => updateSettings({ telegram });

  return (
    <StepView title="Telegram bot API token">
      <TextInput style={{ flex: 1, alignSelf: 'stretch', }} 
        onChangeText={handleChange} />

      <Text style={{ fontSize: 11, paddingTop: 10 }}>See https://www.youtube.com/shorts/yum54mlirPM</Text>
    </StepView>
  )
}

const Step6 = () => {
  const { updateSettings } = useSettingsStore();
  const [accept, setAccept] = useState(false);
  const placeholder = `
This software and its contents are provided "as is" and without warranties of any kind, either expressed or implied. By using this software, you acknowledge and agree that all use of this software is entirely at your own risk.

The creator or distributor of this software makes no representations or warranties regarding the performance, effectiveness, or applicability of any materials or content found within the software. No guarantee or warranty is made that the software will be error-free, secure, or uninterrupted, and the software developer shall not be liable for any damages, whether direct, indirect, incidental, special, consequential or exemplary, that result from the use or inability to use the software.

The user agrees to indemnify and hold harmless the software developer from any claims, damage, loss, or expense, including any attorneys' fees, incurred due to or arising from the use of the software.

Use of this software indicates your acceptance of this disclaimer. If you do not agree to be bound by these terms, then do not download, install, or use the software.
  `;
  const handleChange = (val) => {  
    setAccept(val) 
    updateSettings({ acceptedTerms: val });
  }

  return (
    <StepView title="Terms">
      <TextInput
        value={placeholder}
        editable={false}
        onChangeText={handleChange} 
        multiline
        numberOfLines={10} />

      <View style={{ flexDirection: 'row', justifyContent: 'flex-end' }}>
        <Text style={{ paddingRight: 10 }}>I accept the terms</Text>
        <CheckBox value={accept} onValueChange={handleChange} />
      </View>
    </StepView>
  )
}

export const Wizard = () => {
  const { user, acceptedTerms, updateSettings, /* telegram */ } = useSettingsStore();

  const wizard = useRef(null);
  const [isFirstStep, setIsFirstStep] = useState(true)
  const [isLastStep, setIsLastStep] = useState(false)
  const [step, setStep] = useState(0);

  const steps = [
    { content: <Step0 />},
    { content: <Step1 />},
    // { content: <Step2 />},
    // { content: <Step3 />},
    { content: <Step4 />},
    { content: <Step5 />},
    { content: <Step6 />},
  ]

  const isNextDisabled = () => {
    if (step === 1 && !user.name?.length) return true;
    // if (step === 5 && !telegram) return true;
    if (step === steps.length - 1 && !acceptedTerms) return true;
  }

  return (
    <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center', padding: 10 }}>
      <View style={{ flex: 1, alignItems: 'center', justifyContent: 'center' }}>
        
        <Wizardd
          ref={wizard}
          steps={steps}
          onNext={() => {
            console.log("Next Step Called")
          }}
          onPrev={() => {
            console.log("Previous Step Called")
          }}
          currentStep={({ currentStep, isLastStep, isFirstStep }) => {
            setStep(currentStep);
            setIsFirstStep(isFirstStep);
            setIsLastStep(isLastStep);
          }}
        />
      </View>

      <NavButtons
        isNextDisabled={isNextDisabled()}
        isFirstStep={isFirstStep} 
        isLastStep={isLastStep} 
        onPrev={() => wizard.current.prev()} 
        onNext={() => wizard.current.next()} 
        onAccept={() => updateSettings({ completedWizard: true }) } />
    </View>
  )
}

export default Wizard;
