import * as whisper from "whisper-webgpu";
import toWav from "audiobuffer-to-wav";

let audioMaker = require('audiomaker');
let _audioMaker = new audioMaker();

const S3Q = "https://huggingface.co/soulgarden/models/resolve/main/tts";
const HOST = 'http://localhost:9697';

function splitText(texto, longitudMaxima) {
  const frases = texto.split('.');
  let parrafos = [];
  let parrafoActual = '';

  for (let i = 0; i < frases.length; i++) {
      const frase = frases[i].trim();

      // Agregar el punto al final del prrafo anterior solo si hay algo en el prrafo actual
      if (parrafoActual !== '') {
          parrafoActual += '.';
          parrafos.push(parrafoActual);
          parrafoActual = '';
      }

      if ((parrafoActual + ' ' + frase).length <= longitudMaxima) {
          if (parrafoActual !== '') {
              parrafoActual += ' ';
          }
          parrafoActual += frase;
      } else {
          parrafoActual = frase;
      }
  }

  if (parrafoActual !== '') {
      // Agregar el punto al final del ltimo prrafo solo si hay algo en el prrafo actual
      parrafoActual += '.';
      parrafos.push(parrafoActual);
  }

  return parrafos;
}

async function getImageAsBase64(url) {
  try {
    const response = await fetch(url);
    if (!response.ok) throw new Error('Network response was not ok.');

    const blob = await response.blob();
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onloadend = () => {
        const base64data = reader.result.split(',')[1];
        resolve(base64data);
      };
      reader.onerror = reject;

      reader.readAsDataURL(blob);
    });
  } catch (error) {
    console.error(error);
    return null;
  }
}

const base64ToArrayBuffer = (base64) => {
  const binaryString = window.atob(base64.split(',')[1]);
  const len = binaryString.length;
  const bytes = new Uint8Array(len);
  for (let i = 0; i < len; i++) {
    bytes[i] = binaryString.charCodeAt(i);
  }
  return bytes.buffer;
}

export const fetchBytes = async ({ url, cache = true, onProgress }) => {
  const cache_ = await caches.open("SGMEM");
  const response = await cache_.match(url);

  if (cache && response) {
    const buffer = await response.arrayBuffer();
    return new Uint8Array(buffer);
  }

  return new Promise((resolve, reject) => {
    const request = new XMLHttpRequest();
    request.open("GET", url, true);
    request.responseType = "arraybuffer";

    request.onload = async () => {
      const buffer = request.response;
      const bytes = new Uint8Array(buffer);

      const response = new Response(bytes);
      await cache_.put(url, response.clone());

      resolve(bytes);
    };

    request.onerror = () => reject(request.statusText);

    request.onprogress = (ev = {}) => {
      const { lengthComputable, loaded, total } = ev;
      if (lengthComputable) {
        onProgress?.({ progress: loaded / total });
      }
    };

    request.send();
  });
};

export const processAudio = async ({ input }) => {
  let buffer;

  if (typeof input === "string") {
    if (input.startsWith('http')) {
      const response = await fetch(input);
      buffer = await response.arrayBuffer();
    } else {
      buffer = base64ToArrayBuffer(input);
    }
  } else {
    buffer = await new Blob(input).arrayBuffer();
  }

  const actx = new AudioContext({
    sampleRate: 16000,
    echoCancellation: true,
    autoGainControl: true,
    noiseSuppression: true,
  });

  const resampled = await actx.decodeAudioData(buffer);

  const leftChannel = resampled.getChannelData(0);
  const rightChannel =
    resampled.numberOfChannels > 1 ? resampled.getChannelData(1) : leftChannel;

  const audio = new Float32Array(leftChannel.length);
  for (let i = 0; i < leftChannel.length; i++)
    audio[i] = (leftChannel[i] + rightChannel[i]) / 2;

  const mono = actx.createBuffer(1, audio.length, actx.sampleRate);
  mono.copyToChannel(audio, 0, 0);

  const wav = toWav(mono);

  return { audio, wav };
};

export const listen = async ({
  wav,
  bin = `${S3Q}/model.bin`,
  data = `${S3Q}/config.json`,
  onProgress = (segment) => console.log(segment),
  onInitProgress = (report) => console.log(report),
}) => {
  const tokenizer = await fetchBytes({ url: data });
  const model = await fetchBytes({ url: bin, onProgress: onInitProgress });

  const inputs = new Uint8Array(wav);

  const consolewarn = console.warn;
  console.warn = () => {};
  await whisper.default();
  const builder = new whisper.SessionBuilder();
  const session = await builder.setModel(model).setTokenizer(tokenizer).build();

  const segments = [];
  await session.stream(inputs, false, (segment) => {
    onProgress?.(segment);
    segments.push(segment);
  });
  console.warn = consolewarn;

  session.free();

  return segments.map((segment) => segment.text).join(" ");
};

export const say = async ({ 
  text,
  gender = 'woman',
  backend = 'bark',
  host = HOST,
}) => {
  const model =  gender === 'woman' ? 'v2/en_speaker_9' : 'v2/en_speaker_6'; 
  const paragraphs = splitText(text, 100);
  const blobs = [];
  for (let paragraph of paragraphs) {
    const response = await fetch(`${host}/tts`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
     body: JSON.stringify({ backend, model, input: paragraph })
     // body: JSON.stringify({ model: 'tts-1', input: paragraph })
    });
  
    if (!response.ok)
      throw new Error(`Speak failed with HTTP error: ${response.status}`);
  
    const blob = await response.blob();
    blobs.push(blob);
  }

  const merged = await _audioMaker.add(blobs)
  return merged
};

export const readImage = async ({
  image,
  host = HOST,
  temperature = 0
 }) => {
  const [ one, two ] = image.split(',');
  
  const response = await fetch(`${host}/completion`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify({
      image_data: [{ id: 1, data: two || one }],
      prompt: `<|im_start|>user\n[img-1]\nWhat on the image? Describe it with detail. Determine if its a selfie, a picture, a meme or a drawing. Determine if its NSFW.<|im_end|>\n<|im_start|>assistant\n`,
      temperature,
    })
  });

  if (!response.ok)
    throw new Error(`Watch failed Http error: ${response.status}`);

  const { content } = await response.json();

  return content;
};

export const generateImage = async({ 
  prompt,
  host = HOST,
}) => {
  const data = {
    prompt,
    size: '256x256'
  };

  const response = await fetch(`${host}/v1/images/generations`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: JSON.stringify(data)
  });

  const responseData = await response.json();

  const { data: imageData } = responseData;
  const { url } = imageData[0];

  return url;
}
