import React, { useEffect, useRef } from 'react';

import ChatUI, { Bubble, useMessages } from '@chatui/core';

import { View } from './Basics';

import useSettingsStore from '../store/useSettingsStore';

import '@chatui/core/dist/index.css';
import './Chat.css';

const AVATAR_USER = 'user.png'; 
const AVATAR_AI = 'ai.png';
const LOCALE = 'en-US';

const parseMessage = (message) => {
  const isSoul = message.user.id === 2 
  message.user.avatar = isSoul ? AVATAR_AI : AVATAR_USER;
  message.position = isSoul ? 'left' : 'right';
  message.content.text = message.content.text || message.content.emojis;

  return message;
}

export const Chat = () => {
  const { messages: storedMessages, messageAdd } = useSettingsStore();
  const { messages, appendMsg } = useMessages(storedMessages.map(message => parseMessage(message)));

  const fileInputRef = useRef(null);

  const handleSend = async (type, content) => {

    const id = content.message_id;
    const message = {
      id,

      type,
      content: ['image', 'audio'].includes(type) ? { url: content.href || content } : { text: content },
    };

    messageAdd(message);
  }
    
  const handleFileSelect = (event) => {
    const file = event.target.files[0];
    const [ type ] = file.type.split('/');

    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        handleSend(type, reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleAttachment = () => fileInputRef.current.click();

  const renderMessageContent = (data) => {
    const { type, content, user } = data;
    let bubbleContent = content.text;
    
    if (type === 'audio') {
      let src = content.url || window.URL.createObjectURL(content.blob);
      bubbleContent = <audio src={src} controls></audio>; 

      /*
      if (user.id === 2) {
        bubbleContent = <div>
          <p>{content.text}</p>
          <audio src={src} controls></audio>
        </div>;
      }
      */
    }
      
    if (type === 'image') {
      bubbleContent = <div>
        <img src={content.url} style={{ maxWidth: 200, maxHeight: 200 }} />
      </div>
    }
    
    return <Bubble content={bubbleContent} />;
  }

  const toolbar = [
    {
      type: 'attachment',
      title: 'Attachment',
      img: 'https://cdn-icons-png.flaticon.com/512/3756/3756614.png',
    },
  ];

  useEffect(() => {
    if (!storedMessages.length) return;

    const [ message ] = storedMessages.slice(-1);
    if  (messages[messages.length - 1]?.id !== message.id)
      appendMsg(parseMessage(message));
  }, [storedMessages]);

  return (
    <View style={{ flex: 1 }}>
      <input
        type="file"
        onChange={handleFileSelect}
        style={{ display: 'none' }}
        ref={fileInputRef}
      />

      <ChatUI
        locale={LOCALE}
        wideBreakpoint="600px"
        placeholder='Message'

        toolbar={toolbar}
        onToolbarClick={handleAttachment}
        // inputType='voice'
        inputOptions={{ minRows: 3 }}
        messages={messages}
        renderMessageContent={renderMessageContent}
        onSend={handleSend}
      />
    </View>
  );
}

export default Chat;
