import React, { useState } from 'react';

import { Button, HRule, TextInput, Title, View, CheckBox, Text } from './Basics';
import useSettingsStore from '../store/useSettingsStore';

const Settings = () => {
  const { updateSettings, ...settings } = useSettingsStore();
  const [form, setForm] = useState(settings);
  const handleSubmit = () => updateSettings(form);

  const { user, telegram } = form;
  const nsfw = settings.sexuality === 'sexual';
  return (
    <View style={{ paddingHorizontal: 10 }}>
      <Title>Social Networks</Title>
      <TextInput label="Telegram token" value={telegram} onChangeText={(text) => setForm({ ...form, telegram: text })} />

      <HRule style={{ paddingVertical: 10 }} />

      <Title>User Information</Title>
      <TextInput label="Name" 
        value={user.name} 
        onChangeText={(text) => setForm({ ...form, user: { ...user, name: text }})} />
      
      {false &&
      <View>
        <TextInput label="Appearance" 
          value={user.appearance} 
          onChangeText={(text) => setForm({ ...form, user: { ...user, appearance: text }})} 
          multiline
          numberOfLines={3} />
        
        <TextInput label="About" value={user.about} onChangeText={(text) => setForm({ ...form, user: { ...user, about: text }})} 
          multiline
          numberOfLines={3} />
      </View>
      }

      <View style={{ paddingTop: 10, flexDirection: 'row' }}>
        <Text style={{ paddingRight: 10 }}>Enable NSFW</Text>
        <CheckBox value={nsfw} onValueChange={(val) => setForm({ ...form, sexuality: val? 'sexual' : 'nosex'}) } />
      </View>

      <Button title="Save" onPress={handleSubmit} style={{ marginVertical: 20 }} />
    </View>
  );
};

export default Settings;
