import { useState, useEffect, useReducer } from 'react';

import { ButtonIcon, View, Screen, HRule, COLORS } from './soul/components/Basics';

import Wizard from './soul/components/Wizard';
import Chat from './soul/components/Chat';
import SoulUI from './soul/components/Soul';
import LoaderView from './soul/components/LoaderView';
import Settings from './soul/components/Settings';

import useSettingsStore from './soul/store/useSettingsStore';
import { Soul } from './soul/soul/Soul';
import Telegram from './soul/components/Telegram';

let SOUL;

export default function App() {
  const [_, forceUpdate] = useReducer(x => x + 1, 0);

  const { completedWizard, user, ...soul } = useSettingsStore();

  const [showSettings, setShowSettings] = useState(false);
  const [loaded, setLoaded] = useState(false);
  const [report, setReport] = useState({});

  useEffect(() => {
    SOUL = new Soul(soul);
    return SOUL.subscribe();
  }, []);

  useEffect(() => {
    window?.electronAPI?.onLoaderMessage(report => {
      if (report.log === 'completed')
        setLoaded(true);
      setReport(report)
    });

    if(!window.electronAPI)
      setTimeout(() => setLoaded(100), 3000);
  }, []);

  useEffect(() => {
    window.addEventListener('resize', forceUpdate);

    return () => {
      window.removeEventListener('resize', forceUpdate);
    };
  }, [forceUpdate]);

  return (
    <View style={{ flex: 1 }}>
      
      <Screen visible={true} >
        <View style={{ flex: 1, backgroundColor: COLORS.app }}>
          <View style ={{ flexDirection: 'row', justifyContent: 'space-between', paddingTop: 10, paddingHorizontal: 10 }} >
            { false && <ButtonIcon size="20" icon="x" onClick={() => window?.electronAPI?.quit()} /> }
            <View></View>
            <ButtonIcon size="20" icon="settings" onClick={() => setShowSettings(true)} />
          </View>

          <View style ={{ flexDirection: 'row', justifyContent: 'space-between', alignItems: 'flex-end', padding: 10 }} >
            <SoulUI />
            <Telegram />
          </View>

          <HRule color={COLORS.textSecondary} />

          <Chat />
        </View>
      </Screen>

      <Screen visible={showSettings} closeable onClose={() => setShowSettings(false)} >
        <Settings />
      </Screen>

      <Screen visible={!loaded} >
        <LoaderView report={report} />
      </Screen>

      <Screen visible={!completedWizard}>
        <Wizard />
      </Screen>
    </View>
  );
}
